:root {
    --nav-height: 40px;
  }

html,body {
    height: calc(100% - var(--nav-height));
}

#root {
    height: 100%;
}

main {
    /* min-height: calc(100vh - var(--nav-height)); */
    height: 100%;
    /* justify-content: space-between; */
}

nav {
    height: var(--nav-height);
    background-color: cadetblue;
    align-items: center;
}

.nav-link {
    margin: 5px;
}

.container-v, .button-container-v {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.container-h {
    display: flex;
}

/* .container-h,  */
nav, .test-questions-buttons {
    display: flex;
    flex-wrap: wrap;
}

.test-questions-buttons {
    align-self: flex-start;
}

.heading, .center, .button-container-v  {
    align-self: center;
}

.center-v {
    justify-content: center;
}

.test-list-item {
    padding: 1.5em;
}

.test-list-item > span {
    padding: .5em;
}

.space-around {
    justify-content: space-around;
}

.button-container-v {
    width: 40%;
}

.button-container-v button {
    margin: 10px 0;
}

.test-question-button {
    margin: 5px;
}

.question-text, .answer-options-container, .reading-answer-options-container {
    margin: 2.5%;
    justify-content: center;
    width: 40%;
}

.question-text {
    /* width: 50%; */
    display: flex;
    flex-direction: row;
}

.reading-answer-options-container {
    width: 100%;
}

.reading-passage {
    width: 45%;
}

.dashboard {
    justify-content: center;
}

.center-heading {
    text-align: center;
}

.body-div {
    height: calc(100% - var(--nav-height));
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.test-div {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: inherit;
    margin: .5em 1.5em;
}

.correct {
    color: green;
}

.incorrect {
    color: red;
}

.answer-option, .answer-option-result {
    display: flex;
    margin: .25em;
}

.answer-option-result {
    margin: .5em;
}

.answer-option input[type="radio"] {
    opacity: 0;
    width: 0;
    position: fixed;
}

.answer-option-label {
    border: 1px solid black;
    border-radius: 50%;
    width: 2em;
    height: 2em;
    justify-content: center;
    align-items: center;
    text-align: center;
    display: flex;
}

.answer-option-label:hover {
    background-color: green;
}

.answer-options input[type="radio"]:checked + .answer-option-label {
    background-color: green;
    opacity: 90%;
}

.answer-option-text {
    /* vertical-align: middle; */
    display: flex;
    align-items: center;
    margin-left: .25em;
}

.directional-btn-container {
    justify-content: space-between;
}

.question-number {
    margin-right: .5em;
}

.reading-container {
    width: 50%;
    padding: 1em;
}

.section-btns, .response-table-data {
    text-align: center;
}

.section-btns > button {
    margin: 1em;
}

.directional-btn-container {
    margin: 1em;
}

.scores {
    justify-content: center;
}

.scores * {
    margin: 1em;
}

.timer {
    border: 1px solid black;
    padding: 1em;
    margin: .5em;
    width: 10%;
}